/* global chrome */
import React from 'react';
import ReactDOM from 'react-dom';
import App from './views/Popup/App';
import * as serviceWorker from './serviceWorker';
import {
  genSet,
  gen,
} from './views/Popup/graphql/LocalStorage.js';

const SELECTED_TEXT = 'selected_text';
const getCurrentSelectedText = new Promise(resolve => chrome.tabs.executeScript( {
  code: "window.getSelection().toString();"
}, resolve));

Promise.all([gen(SELECTED_TEXT), getCurrentSelectedText]).then(
  ([prevSelectedText, currentSelectedText]) => {
    let selectedText = "";
    if (prevSelectedText === undefined) {
      prevSelectedText = '';
    }
    if (currentSelectedText !== undefined
        && prevSelectedText !== currentSelectedText[0]) {
        selectedText = currentSelectedText[0];
        genSet(SELECTED_TEXT, currentSelectedText[0]);
    }
    ReactDOM.render(
      <React.StrictMode>
        <App selectedText = {selectedText}/>
      </React.StrictMode>,
      document.getElementById('root')
    );
});

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
