import React from 'react';
import { useState, useRef } from 'react';
import genCurrentPageURL from './util/genCurrentPageURL';

import './App.css';

const IFRAME_URL = "https://www.internalfb.com/intern/til/iframe_embed";

function App(props) {
  const [isIFrameLoading, setIsIFrameLoading] = useState(true);
  const iframeRef = useRef(null);

  return  (
    <div className="App">
      {isIFrameLoading ? (
        <div className="AppLoader">
          <div>
            <h1>Loading TIL...</h1>
          </div>
        </div>
      ) : null}
      <iframe
        ref={iframeRef}
        onLoad={() => {
          setIsIFrameLoading(false);
          genCurrentPageURL(([pageURL, pageTitle]) => {
                const messageContent = {url : pageURL, text: props.selectedText, title: pageTitle};
                const message = JSON.stringify(messageContent);
                const iframe = document.getElementsByClassName('AppIFrame');
                iframe[0].contentWindow.postMessage(message, IFRAME_URL);
          });
        }}
        class="AppIFrame"
        title="iframe"
        style={{
          display: isIFrameLoading ? 'none' : 'block'
        }}
        frameborder="0"
        src={IFRAME_URL}
      />
    </div>
  )
}

export default App;
